<template>
  <div class="wrap">
    <!-- 头部 -->
    <div class="info-header">
      <div><span class="fz14 yellow-border">{{info.address}}</span></div>
      <div><img class="sunny" src="../../assets/image/Sunny.png"></div>
      <div>{{info.temperature}}℃</div>
      <div class="fz12">{{info.temRange}}℃</div>
      <div class="fz12">{{info.date}}</div>
    </div>
    <div class="price-box">
      <div v-for="(item,index) in price" class="price-item" >
        <span v-if="index == 0" class="green">{{item}}</span>
        <span v-else class="fz12">{{item}}#&nbsp;<span class="red">****</span></span>
        <span v-show="index !== price.length -1 && index !== 0" class="border"></span>
      </div>
    </div>
    <!-- 轮播介绍 -->
    <div style="position: relative;">
      <mt-swipe :auto="4000" class="banner-swipe">
        <mt-swipe-item>
          <img src="../../assets/image/1.jpg">
        </mt-swipe-item>
        <mt-swipe-item>
          <img src="../../assets/image/2.jpg">
        </mt-swipe-item>
        <mt-swipe-item>
          <img src="../../assets/image/3.jpg">
        </mt-swipe-item>
      </mt-swipe>
      <div class="white-tip fz12">
        <img src="../../assets/image/循环.png">
        <span>&nbsp;中国人寿财险服务大厅</span>
      </div>
      <div class="white-tip tip-icon">
        <img src="../../assets/image/咨询.png">
      </div>
    </div>
    <!-- 导引 -->
    <div class="icon-nav">
      <div
        class="nav-item"
        v-for="(item, index) in iconNav" 
        :key="index"
        @click="goPage(item.hrefName)"
      >
        <div>
          <img class="icon" :src="item.icon">
        </div>
        <span class="name">{{item.name}}</span>
      </div>
    </div>
    <div class="icon-nav image-nav">
      <div
        class="imag-nav-item"
        v-for="(item, index) in imgeNav" 
        :key="index"
        @click="goPage(item.hrefName)"
      >
        <div>
          <img class="icon" :src="item.icon">
        </div>
      </div>
    </div>
    <div class="icon-nav">
      <div class="title-wrap">
        <div class="header-title hot-title">热销产品</div>
        <div class="header-title more-link"><a>查看更多</a></div>
      </div>
      <div
        class="adv-nav-item"
        v-for="(item, index) in advNav" 
        :key="index"
        @click="goPage(item.hrefName)"
      >
        <img class="icon" :src="item.icon">
        <div class="adv-tip">
          <p class="title">安心驾</p>
          <span class="grand fz12">意外身故/伤残/意外医疗</span>
          <div class="grand fz12 pt10">
            <span class="red-price">￥45&nbsp;</span>起
          </div>
        </div>
      </div>
    </div>
    <div class="info-box">
      <p class="header-title more">更多精彩</p>
      <div class="tab-box">
        <div v-for="(item, index) in tab" :key="index" class="tab-item" @click="changeTab(index)">
          <span
            :class="{'active': activeIndex == index}">
            {{item}}
          </span>
          <div class="green-border" v-show="activeIndex == index"> </div>
        </div>
      </div>
      <div v-for="(item, index) in textNav" :key="index" @click="goPage(item.hrefName)" class="text-nav">
        <div class="info-wrap">
          <div class="title">
            <span>{{item.title}}</span>
          </div>
          <p class="date">{{item.date}}</p>
        </div>
        <div class="img-wrap">
          <img :src="item.img">
        </div>  
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      iconNav: [
        {icon: require('../../assets/image/热门.png'), name: '买车险', hrefName: 'tip'},
        {icon: require('../../assets/image/客服.png'), name: '查保单', hrefName: 'tip'},
        {icon: require('../../assets/image/安全.png'), name: '办理赔', hrefName: 'tip'},
        {icon: require('../../assets/image/收藏.png'), name: '理赔查询', hrefName: 'tip'},
        {icon: require('../../assets/image/热门.png'), name: '保单验真', hrefName: 'tip'},
        {icon: require('../../assets/image/安全.png'), name: '网点导航', hrefName: 'tip'},
        {icon: require('../../assets/image/客服.png'), name: '祝福家', hrefName: 'tip'},
        {icon: require('../../assets/image/收藏.png'), name: '更多功能', hrefName: 'tip'}
      ],
      imgeNav: [
        {icon: require('../../assets/image/test.jpg'), hrefName: 'tip'},
        {icon: require('../../assets/image/test.jpg'), hrefName: 'tip'},
        {icon: require('../../assets/image/test.jpg'), hrefName: 'tip'}
      ],
      advNav: [
        {icon: require('../../assets/image/adv1.jpg'), hrefName: 'tip'},
        {icon: require('../../assets/image/adv2.jpg'), hrefName: 'tip'},
        {icon: require('../../assets/image/adv1.jpg'), hrefName: 'tip'}
      ],
      textNav1: [
        {img: require('../../assets/image/海报1.jpg'), title: '生日快乐|今天，中国人寿喜迎70华诞！', hrefName: 'tip', date: '2019-10-28'},
        {img: require('../../assets/image/5.jpg'), title: '纪实|10分钟，读懂中国人寿故事！', hrefName: 'tip', date: '2019-10-28'},
        {img: require('../../assets/image/6.jpg'), title: '超燃|中国人寿庆70周年主题歌曲《时间的托付》重磅发布！', hrefName: 'tip', date: '2019-10-28'}
      ],
      textNav2: [
        {img: require('../../assets/image/7.jpg'), title: '特惠|居家无忧财险十月特惠，保费不变，保障翻倍！', hrefName: 'tip', date: '2019-10-28'},
        {img: require('../../assets/image/6.jpg'), title: '感动|扎根在大山深处的“保险”书记！', hrefName: 'tip', date: '2019-10-28'},
        {img: require('../../assets/image/海报1.jpg'), title: '活动|感恩父母，从守护健康开始!', hrefName: 'tip', date: '2019-10-28'}
      ],
      textNav: [],
      tab: ['公司新闻', '更多动态'],
      activeIndex: 0,
      price: ['今日油价', '92', '95', '0'],
      info: {
        address: '广州',
        temperature: '13',
        date: '星期一',
        temRange: '晴18~'
      }
    }
  },
  mounted () {
    this.textNav = this.textNav1
  },
  methods: {
    goPage (name) {
      this.$router.push({name: name})
    },
    changeTab (index) {
      this.activeIndex = index
      this.textNav = index == 0 ? this.textNav1 : this.textNav2
    }
  }
}

</script>
<style >
.wrap {
  margin-bottom: 80px;
  text-align: left;
}
.icon-nav {
  text-align: center;
  width: 100%;
}
.icon-nav .nav-item {
  font-size: 12px;
  padding-top: 20px;
  display: inline-block;
  width: 25%;
}
.icon-nav .nav-item .icon{
  height: 35px;
  width: 35px;
}
.icon-nav .imag-nav-item {
  font-size: 12px;
  padding: 20px 10px 0px 0px;
  display: inline-block;
  width: 31%;
  box-sizing: border-box;
}
.icon-nav .imag-nav-item .icon {
  width: 100%;
  height: 100px;
}
.image-nav{
  padding-left: 10px;
  box-sizing: border-box;
}
.image-nav .imag-nav-item img{
  border-radius: 5px;
}
.title-wrap {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  padding: 10px 15px 0 15px;
}

.text-nav {
  width: 100%;
  display: flex;
  margin: 20px 0;
}
.text-nav img{
  width: 80px;
  height: 100px;
}
.text-nav .img-wrap {
  margin: 0 30px;
}
.text-nav .info-wrap {
  flex: 1;
  margin-left: 10px;
  position: relative;
}
.text-nav .title {
  font-size: 16px;
  font-weight: bold;
}
.text-nav .date {
  color: #999;
  font-size: 13px;
  position: absolute;
  bottom: 0;
  margin-bottom: 0px;
}
.header-title{
  font-size: 14px;
  font-weight: bold;
}
.banner-swipe {
  width: 100%;
  box-sizing: border-box;
  height: 200px;
  position: relative;
}
.banner-swipe img{
  height: 100%;
  width: 100%;
}
.white-tip {
  top: 10px;
  left: 15px;
  position: absolute;
  width: auto;
  height: 17px;
  line-height: 17px;
  padding: 3px;
  color: white;
  background-color: #fbfdff66;
  border-radius: 15px;
}
.tip-icon {
  right: 15px;
  left: auto;
}
.white-tip>img{
  width: 15px;
  vertical-align: middle;
}
.more-link {
  color: #0fca61;
  text-align: right;
  flex: 1;
}
.hot-title {
  width: 50%;
  text-align: left;
}
.info-box {
  width: 100%;
}
.adv-nav-item {
  padding: 15px;
  height: 100px;
  position: relative;
}
.adv-tip {
  position: absolute;
  height: 80px;
  top: 10px;
  left: 30px;
  color: #e8e8e8;
  text-align: left;
}
.adv-tip .red-price {
  color: red;
  font-size: 16px;
}
.adv-tip .title {
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: bold;
  color: black;
}
.pt10 {
  padding-top: 10px;
}
.adv-nav-item img {
  height: 100px;
  width: 100%;
  border-radius: 2px;
}
.tab-item {
  width: 50%;
  text-align: center;
  color: #999;
  display: inline-block;
  margin-bottom: 5px;
}
.tab-box {
  border-bottom: 1px solid #eee;
}
.active {
  color: black;
  font-size: 16px !important;
  font-weight: bold;
}
.more {
  margin: 0px 10px 15px 15px;
}
.green {
  color: #0fca61;
}
.red {
  color: red;
}
.border {
  position: absolute;
  display: inline-block;
  width: 1px;
  height: 13px;
  background: #999;
  vertical-align: middle;
  right: 0;
  top: 2px;
}
.price-box {
  width: 100%;
  display: flex;
  padding: 10px 0px;
  text-align: center;
}
.price-item {
  position: relative;
  flex: 1;
}
.fz14 {
  font-size: 14px;
}
.info-header {
  display: flex;
  margin: 0px 20px;
  height: 30px;
  line-height: 30px;
  border-radius: 5px;
  background:-webkit-linear-gradient(to right, red, blue, green); 
  background:linear-gradient(to right,red,blue, green);
  color: white;
}
.info-header>div {
  flex: 1;
  text-align: center;
}
.yellow-border {
  border: 1px solid #e8e85b;
  color: #e8e85b;
  padding: 2px;
}
.fz12 {
  font-size: 12px;
}
.green-border {
  top: 3px;
  position: relative;
  margin: auto;
  width: 30px;
  height: 4px;
  background: #0fca61;
}
.sunny {
  line-height: 30px;
  position: relative;
  top: 3px;
  width: 23px;
}
@media only screen and (-webkit-device-pixel-ratio: 3) and (device-height: 812px) and (device-width: 375px) {
  .wrap {
    margin-bottom: 114px;
  }
}
</style>




