<!--底部tab切换-->
<template>
  <div>
    <div class="tabbar reim_tabbar">
      <a 
        @click="changeFooterNav(item, i)" v-for="(item, i) in navData"
        :key="i"
        class="tabbar-item"
        :class="{'bar-item_on':actIndex == i}"
      >
          <span class="tabbar-span">
              <div class="ic_reimindex" :class="[actIndex == i ? item.class[1] : item.class[0]]"></div>
              <span class="badge active" v-if="item.num">{{item.num}}</span>
          </span>
          <p class="tabbar-label">{{ item.text }}</p>
      </a>
  </div>
  </div>
</template>
<script>
export default {
  name: 'footerNav',
  props: {
    navData: {
      type: Array,
      defalt () {
        return []
      }
    },
    actIndex: {
      type: Number,
      defalt: 0
    }
  },
  data () {
    return {
    }
	},
  methods: {
    changeFooterNav (item, i) {
      if (this.actIndex != i) {
        this.$emit('select', i)
      }
    }
  }
}

</script>

<style type="text/css">
	/*底部导航tab开始*/
	.tabbar_heaght{
		height: 50px;
	}
	.tabbar {
		position: fixed;
		bottom: 0;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		width: 100%;
		background-color: #FFF;
		z-index: 9;
	}
	.tabbar:before {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		border-top: 1px solid #e5e5e5;
		-webkit-transform: scaleY(0.5);
		transform: scaleY(0.5);
	}
	.tabbar-item {
		display: block;
		-webkit-box-flex: 1;
		-webkit-flex: 1;
		flex: 1;
		padding: 5px 0 0;
		font-size: 0;
		color: #999;
		text-align: center;
		cursor: pointer;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	}
	.tabbar-span{
		display: inline-block;
		position: relative;
	}
	.tabbar-item.bar-item_on .tabbar-label{
		color:#00928B;
	}
	.badge {
		display: inline-block;
		padding: 2px 5px;
		min-width: 8px;
		border-radius: 18px;
		background-color: #EB5342;
		color: #FFF;
		line-height: 1.2;
		text-align: center;
		font-size: 12px;
		vertical-align: middle;
	}
	.badge.active{
		position: absolute;
		top: -2px;
		right: -13px;
	}
	.tabbar-label {
		text-align: center;
		color: #0A1735;
		font-size: 10px;
		line-height: 1;
    margin-top: 5px;
	}
  .tabbar-span .ic_reimindex{
    width: 30px;
    height: 30px;
    background-size: 100% !important;
  }
	@media only screen and (-webkit-device-pixel-ratio: 3) and (device-height: 812px) and (device-width: 375px) {
		.tabbar {
			border-bottom: 34px solid #fff;
		}
	}
</style>


