<template>
  <div class="wrap">
    <transition
      name="router-fade"
      mode="out-in"
    >
      <router-view />
    </transition>
    <div class="nav_box_fixed btn_box">
      <footer-nav :nav-data="homeFooterList" :actIndex="actIndex" @select="select"/>
    </div>
  </div>
</template>
<script>
import FooterNav from '../../components/footerNav'
export default {
  name: 'Main',
  components: {
    FooterNav
  },
  data () {
    return {
      homeFooterList: [
        {class: ['ic_tabar1', 'ic_tabar1_active'], text: '首页'},
        {class: ['ic_tabar2', 'ic_tabar2_active'], text: '待阅'},
        {class: ['ic_tabar3', 'ic_tabar3_active'], text: '我的'}
      ],
      actIndex: 0
    }
  },
  watch: {
    '$route': {
      handler (val, old) {
        let routeName = val.name
        let nameMap = ['demo', 'tip', 'hi']
        this.actIndex = nameMap.indexOf(routeName)
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    select (index) {
      let nameMap = ['demo', 'tip', 'hi']
      let name = nameMap[index]
      this.actIndex = index
      this.$router.push({name: name})
    }
  }
}

</script>
<style>
.ic_tabar1 {
  background: url(../../assets/image/ico1.png) no-repeat;
}
.ic_tabar1_active {
  background: url(../../assets/image/ico1_active.png) no-repeat;
}
.ic_tabar2 {
  background: url(../../assets/image/ico2.png) no-repeat;
}
.ic_tabar2_active {
  background: url(../../assets/image/ico2_active.png) no-repeat;
}
.ic_tabar3 {
  background: url(../../assets/image/ico3.png)  no-repeat;
}
.ic_tabar3_active {
  background: url(../../assets/image/ico3_active.png) no-repeat;
}
</style>



