import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
// import Home from '@/views/home'
// import DetailPage from '@/views/detail'
// import SearchPage from '@/views/search'
import demo from '@/views/demo/home'
import demoIndex from '@/views/demo/index'
import white from '@/views/demo/white'
import hi from '@/views/demo/hi'
Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'Home',
    //   component: Home,
    // },
    // {
    // 	path: '/detail/:id',
    // 	component: DetailPage
    // },
 	  // {
    //   path: '/search',
    //   name: 'MovieSearch',
    //   component: SearchPage,
    // },
    {
      path: '/',
      name: 'demoIndex',
      component: demoIndex,
      redirect: '/demo',
      children: [
        {
          path: 'demo',
          name: 'demo',
          component: demo
        },
        {
          path: 'tip',
          name: 'tip',
          component: white
        },
        {
          path: 'hi',
          name: 'hi',
          component: hi
        }
      ]
    }
  ]
})



